@charset "UTF-8";
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

ul, ol {
  padding-left: 27px; }

* {
  box-sizing: border-box; }

img {
  max-width: 100%; }

video {
  width: 100%;
  display: block; }

body {
  line-height: 1.5;
  font-family: "Open Sans", sans-serif;
  font-weight: 100;
  font-size: 14px;
  color: #333; }

.container {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 13.5px; }

[class^="icon-"] span,
[class*=" icon-"] span {
  position: absolute;
  left: -9999px; }

svg {
  display: inline-block;
  fill: currentColor !important;
  vertical-align: middle; }
  svg.svg-icon {
    height: 1em;
    width: 1em; }

hr {
  border: none;
  border-top: 1px dotted #ccc; }

.site-body {
  position: relative; }
  .site-body.overlay:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1; }

.clearfix:after {
  content: ".";
  visibility: hidden;
  display: block;
  height: 0;
  clear: both; }

h1, h2, h3 {
  line-height: 1;
  font-family: monospace;
  color: #E91E63;
  font-weight: 100;
  padding-top: 13.5px; }

h1 {
  font-size: 36px;
  margin-bottom: 27px; }

h2 {
  font-size: 24px;
  margin-bottom: 13.5px; }

h3 {
  font-size: 18px;
  padding-top: 13.5px;
  margin-bottom: 13.5px;
  color: #000000; }

h4 {
  font-size: 16px;
  padding-top: 13.5px;
  margin-bottom: 13.5px;
  font-style: italic; }

p {
  font-size: 14px;
  line-height: 1.5;
  margin-bottom: 13.5px; }

ul, ol {
  margin-bottom: 13.5px; }

li {
  line-height: 1.5; }

a {
  color: #E91E63;
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }

.site-header {
  text-align: center;
  background-image: url("/img/svg/header-bg.svg");
  background-size: cover;
  background-color: #002835;
  color: #ffffff;
  padding: 0 13.5px 27px; }
  @media screen and (min-width: 700px) {
    .site-header {
      padding-bottom: 54px; } }

.site-header--slim .site-header__desc {
  margin-bottom: 0; }

.logo .svg-icon {
  height: 7em;
  width: 7em; }
  @media screen and (min-width: 700px) {
    .logo .svg-icon {
      margin-bottom: 13.5px;
      height: 11em;
      width: 11em; } }

.site-header__title {
  color: #ffffff;
  display: block;
  min-height: 24px;
  font-size: 24px;
  line-height: 1.2;
  margin-bottom: 13.5px; }
  @media screen and (min-width: 700px) {
    .site-header__title {
      min-height: 43px;
      font-size: 36px; } }

.site-header__desc {
  font-size: 14px;
  margin: 0 auto 27px;
  width: 100%;
  max-width: 700px; }
  @media screen and (min-width: 700px) {
    .site-header__desc {
      font-size: 18px; } }

.site-header__search {
  margin: 27px auto 0;
  max-width: 700px; }

.site-header__input {
  border-color: #E91E63;
  background-color: transparent;
  color: #ffffff;
  max-width: 100%;
  padding-left: 3em;
  background-image: url("/img/svg/search.svg");
  background-repeat: no-repeat;
  background-size: 1.5em;
  background-position: 1em center; }

.curser {
  animation: blink 1s infinite;
  display: block;
  margin-top: 27px; }
  .curser .svg-icon {
    width: 1em;
    height: 2em;
    color: #E91E63; }

@keyframes blink {
  to {
    opacity: 0; } }

.site-footer .svg-icon {
  width: 1.5em;
  height: 1.5em;
  color: #ffffff; }

.copyright {
  text-align: center;
  background: #000000;
  padding: 27px; }

.copyright__text {
  color: #cccccc;
  margin-bottom: 13.5px; }

.os__text {
  color: #cccccc;
  margin-bottom: 13.5px; }

.sponsor {
  margin-top: 10px; }
  .sponsor a:hover {
    text-decoration: none; }

.sponsor__logo {
  height: 20px;
  padding-right: 20px; }

.menu-icon {
  padding: 13.5px; }
  .menu-icon .svg-icon {
    width: 1.5em;
    height: 1.5em; }
  @media screen and (min-width: 700px) {
    .menu-icon {
      display: none; } }

.site-nav {
  display: flex;
  flex-wrap: wrap;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  max-width: 1024px;
  margin: 0 auto 27px; }
  @media screen and (min-width: 700px) {
    .site-nav {
      margin-bottom: 54px;
      justify-content: center; } }
  .site-nav .social-nav {
    padding: 0;
    margin-left: auto; }
    @media screen and (min-width: 700px) {
      .site-nav .social-nav {
        order: 2;
        margin-left: auto;
        display: none; } }
    @media screen and (min-width: 960px) {
      .site-nav .social-nav {
        display: flex; } }
    .site-nav .social-nav .svg-icon {
      width: 1.5em;
      height: 1.5em;
      color: #ffffff; }
  .site-nav .social-nav__link:hover .svg-icon {
    color: #E91E63; }

.site-nav__list {
  margin: 0;
  list-style: none;
  padding: 0;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  display: none;
  width: 100%; }
  @media screen and (min-width: 700px) {
    .site-nav__list {
      display: flex;
      justify-content: center;
      border: none; } }
  @media screen and (min-width: 960px) {
    .site-nav__list {
      width: auto;
      justify-content: flex-start; } }
  .site-nav__list.active {
    display: block; }

.site-nav__item {
  text-align: left; }
  @media screen and (min-width: 700px) {
    .site-nav__item {
      display: flex;
      align-items: center;
      border-right: 1px solid rgba(255, 255, 255, 0.2);
      flex-grow: 1; }
      .site-nav__item:last-child {
        border: none; } }

.site-nav__link {
  display: block;
  width: 100%;
  color: #ffffff;
  text-decoration: none;
  transition: all ease .3s;
  padding: 13.5px; }
  @media screen and (min-width: 700px) {
    .site-nav__link {
      padding: 13.5px 8px;
      text-align: center; } }
  @media screen and (min-width: 960px) {
    .site-nav__link {
      padding: 13.5px; } }
  .site-nav__link:hover {
    color: #E91E63;
    text-decoration: none; }

